header {
  text-align: center;
  background-color: #c9a4d9;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

header .logo {
  align-items: center;
  gap: 15px;
  margin-left: 5px;
  display: flex;
}

header .logo img {
  width: auto;
  height: auto;
  max-height: 75px;
  display: block;
}

header .empresa {
  margin-right: 5px;
}

.navegacion {
  text-align: center;
  background-color: #cbe2f6;
  margin: 5px 0;
}

.menu {
  text-align: center;
  background-color: #eee;
  border: 1px solid #b0aeb3;
  align-content: center;
  margin: 0;
}

.main {
  grid-template-columns: 1fr 3fr;
  gap: 10px;
  padding-inline: 0;
  display: grid;
}

.main .contenido {
  background-color: #e2d7f2;
  grid-template-rows: 1fr 4fr;
  gap: 15px;
  display: grid;
}

.main .contenido .descripcion {
  text-align: center;
  background-color: #d081a8;
  border-radius: 10px;
  align-content: center;
  margin: 10px 10px 0;
}

.main .contenido .productos, .main .contenido .productos .producto {
  text-align: center;
  background-color: #c1658c;
  border-radius: 10px;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
  margin: 0 10px 10px;
  display: grid;
}

.main .contenido .productos .producto {
  background-color: #f3b6c1;
  place-content: center;
  align-items: center;
}

.main .contenido .productos .producto img {
  width: 50px;
  height: auto;
  margin-bottom: 10px;
}

.testimonios {
  text-align: center;
  background-color: #e2d7f2;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
  margin: 5px 0;
  display: grid;
}

.testimonios .testimonio {
  text-align: center;
  background-color: #d2b5dd;
  border-radius: 10px;
  place-content: center;
  align-items: center;
  display: flex;
}

footer {
  text-align: center;
  background-color: #cbe2f6;
  margin: 5px 0;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #eee;
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 2%;
}

.titulo {
  grid-column: 1 / -1;
}

header, .navegacion, .main, .descripcion, .productos, .producto, .testimonio, .testimonios, footer {
  padding: 1.2rem;
}

header, .navegacion, .descripcion, .productos, .menu, .testimonios, footer {
  margin-bottom: .5rem;
  font-size: 1rem;
}
